.proccessHeader{
    font-size: 2vw;
    color: #124770;
    font-weight: 500;
    text-align: center;  
}

.proccessHeaderMobile{
    font-size: 3.5vh;
    color: #124770;
    text-align : 'right';
    font-family: 'Segoe UI Regular';
    font-weight: 600;
}

.Timeline{
    background-color: #124770;
    height: 1px;
    width: 1vw;
    border-radius: 15px;
    z-index: 2;
    position: relative;
}

.BranchRight{
    background-color: #124770;
    height: 0px;
    width: 0px;
    position: relative;
}

.BranchLeft{
    position: relative;
    height: 0px;
    width: 0px;
    display: flex;
    flex-direction: row-reverse;
    
}